.show-mobile {
    @media (max-width: 995px) {
        display: block !important;
    }
    @media (min-width: 996px) {
        display: none !important;
    }
}

.show-pc {
    @media (min-width: 996px) {
        display: block !important;
    }
    @media (max-width: 995px) {
        display: none !important;
    }
}

//clearfix
.clearfix {
    &::before,
    &::after {
        content: "";
        display: table;
    }
    &::after {
        clear: both;
    }
}